.pricing {
  background-color: $black-bg-color;
  padding: 224px 0 141px 0;
  @include sm-md {
    padding: 80px 0 60px 0;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    text-align: center;
    color: $basic-white;
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-top: 14px;
    text-align: center;
  }

  &__blocks {
    display: flex;
    margin-top: 106px;
    @include sm-md {
      margin-top: 80px;
      flex-direction: column;
    }
  }

  &__block {
    padding: 50px 40px;
    border-radius: 10px;
    background-color: $basic-white;
    margin-right: 30px;
    position: relative;
    @include sm-md {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__block:last-child {
    margin-right: 0;
  }

  &__new {
    position: absolute;
    top: -26px;
    right: 23px;
    @include sm-md {
      top: -14px; 
      right: 16px;
    }

  }

  &__header {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: $basic-black;
    text-align: center;
  }

  &__description {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: $basic-grey;
    margin: 35px 0;
  }

  &__cost {
    text-align: center;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    color: $basic-blue;
  }

  &__period {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: $blue-light;
    text-align: center;
    margin-bottom: 36px;
  }

  &__btn {
    background-color: $basic-blue;
    color: $basic-white;
    padding: 15px 0px;
    justify-content: center;
    display: flex;
    font-style: normal;
    font-weight: 700;
    border: 2px solid $basic-blue;
    letter-spacing: 0.2px;
    font-size: 14px;
    border-radius: 5px;
    margin-bottom: 36px;
    transition: all .2s ease-in-out;
  }

  &__btn:hover {
    border: 2px solid $basic-blue;
    color: $basic-black;
    background: none;
  }

  &__list {
    list-style-type: none;
  }

  &__item {
    display: flex;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    padding-bottom: 17px;
  }

  &__item p {
    display: flex;
    margin: auto 0;
    max-width: 71%;
  }

  &__icon {
    height: 100%;
    margin: auto 10px auto 0;
  }
}