.industry {
  background-color: $blue-bg-color;
  padding: 112px 0;
  color: $basic-white;

  @include sm-md {
    padding: 80px 0;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    text-align: center;
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    margin-top: 14px;
    text-align: center;
  }

  &__reviews {
    margin-top: 120px;
    margin-bottom: 80px;
    display: flex;
    justify-content: space-between;

    @include sm-md {
      margin-top: 60px;
      margin-bottom: 0;
      flex-direction: column;
    }
  }
  
  &__review {
    background-color: $basic-white;
    color: $basic-black;
    text-align: left;
    border-radius: 10px;
    padding: 30px 40px;
    margin-right: 55px;
    position: relative;
    transition: all .2s ease-in-out;
    @include sm-md {
      margin-right: 0;
      margin-bottom: 20px;
    }
  }


  &__review:last-child {
    margin-right: 0;
  }

  &__review:hover {
    -webkit-box-shadow: 5px 5px 29px -10px rgba(34, 60, 80, 0.6);
    -moz-box-shadow: 5px 5px 29px -10px rgba(34, 60, 80, 0.6);
    box-shadow: 5px 5px 29px -10px rgba(34, 60, 80, 0.6);
  &__info {
    display: flex;
    &-block {
    display: flex;
    flex-direction: column;
    margin: auto 0;
    padding-left: 13px;
    }
  }


  &__name {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
  }

  &__position {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    color: $basic-grey;
  }

  &__stars {
    margin: 15px 0;
  }

  &__message {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    color: $basic-grey;
  }
}
}