.footer {
  background-color: $basic-black;
  color: $basic-white;
  padding: 50px 0 0 0;

  &__blocks {
    display: flex;
    justify-content: space-between;
    @include sm-md {
      flex-direction: column;
    }
  }

  &__block {
    @include sm-md {
      margin-bottom: 20px;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    padding-bottom: 5px;
    @include sm-md {
      padding-bottom: 0;
    }
  }

  &__list {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    list-style: none;
  }

  &__item {
    margin-top: 10px;
    display: flex;
    @include sm-md {
      margin-top: 5px;
    }
  }

  &__icon {
    margin-right: 10px;
    height: 100%;
  }

  &__link {
    color: $basic-white;
    margin: auto 0;
  }

  &__line {
    background-color: $basic-white;
    color: $basic-black;
    margin-top: 50px;
    @include sm-md {
      margin-top: 20px;
    }
  }

  &__container {
    max-width: 1050px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    padding: 25px 15px;
    @include sm-md {
      flex-direction: column;
      background-color: $basic-black;
      padding: 15px;
    }
  }

  &__social-icon {
    margin-left: 10px;
  }

  &__policy {
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    color: $basic-grey;
    @include sm-md {
      color: $basic-white;
      font-weight: 400;
      margin-bottom: 20px;
    }
  }

}