.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  letter-spacing: 0.6px;
  color: $basic-white;
  background-color: $basic-black;

  &__body {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
  }

  &__logo {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: $basic-white;
    position: relative;
    z-index: 3;
  }

  &__list {
    display: flex;
    position: relative;
    z-index: 2;

    @include sm-md {
      display: block;
    }
  }

  &__item {
    list-style: none;
    margin: 0 0 0 20px;
    
    @include sm-md {
      margin: 0 0 20px 0;
      text-align: center;
      display: block;
    }
  }

  &__link::before {
    content: '';
    bottom: 0;
    left: 0;
    position: absolute;
    width: 100%;
    height: 1.3px;
    background-color: rgb(255, 255, 255);
    transition: 0.2s;
    transform: scaleX(0);
  }
  &__link:hover:before {
    transform: scaleX(1);
  }

  &__link {
    color: $basic-white;
    font-style: normal;
    font-weight: 400;
    letter-spacing: 0.2px;
    font-size: 14px;
    line-height: 24px;
    padding-bottom: 5px;

    @include sm-md {
      font-style: normal;
      font-weight: 400;
      font-size: 30px;
      line-height: 45px;
    }
  }

  &__left {
    position: relative;
    z-index: 3;
    display: none;
    margin-left: auto;

    img {
      padding-right: 20px;
    }

    @include sm-md {
      display: flex;
    }
  }

  &__burger {
    display: block;
    position: relative;
    width: 30px;
    height: 20px;
    display: none;
    z-index: 3;

    @include sm-md {
      display: block;
    }
  }

  &__burger.active span{
    transform: scale(0);
  }

  &__burger.active:before {
    transform: rotate(45deg);
    top: 9px;
  }

  &__burger.active:after {
    transform: rotate(-45deg);
    bottom: 9px;
  }

  &__burger span {
    position: absolute;
    background-color: $basic-white;
    left: 0;
    width: 100%;
    height: 2px;
    top: 8px;
    transition: all .2s ease 0s;
  }

  &__burger:before,
  &__burger:after {
    content: '';
    background-color: $basic-white;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all .2s ease 0s;
  }

  &__burger:before {
    top: 0;
  }

  &__burger:after {
    bottom: 0;
  }

  &__menu {
    display: contents;
    @include sm-md {
      display: block;
      position: fixed;
      top: -100%;
      overflow: auto;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: $black-bg-color;
      padding-top: 80px;
      transition: all .2s ease 0s;
      padding-top: 100px;
    }
  }
  &__menu.active {
    top: 0;
  }
}

.right-list {
  @include sm-md {
    display: none;
  }
}

.header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 2;

  @include sm-md {
    background-color: $black-bg-color;

  }
}

