.consult {
  padding: 40px 0;
  @include sm-md {
    padding: 20px 0;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    @include sm-md {
      flex-direction: column;
    }
  }

  &__left {
    @include sm-md {
      text-align: center;
    }
  }

  &__right {
    display: flex;
    @include sm-md {
      justify-content: center;
    }
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    color: $basic-black;
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    color: $basic-grey;
    @include sm-md {
      margin: 11px 0;
    }
  }

  &__btn {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    color: $basic-white;
    background-color: $basic-blue;
    border-radius: 5px;
    padding: 17px 40px;
    border: 2px solid $basic-blue;
    transition: all .2s ease-in-out;
  }

  &__btn:hover {
    border: 2px solid $basic-blue;
    color: $basic-black;
    background: none;
  }
}