.blue-btn {
  background-color: $basic-blue;
  border: 2px solid $basic-blue;
  color: $basic-white;
  padding: 13px 36px;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 0.6px;
  border-radius: 23px;
  transition: all .2s eease-in-out;
}

.blue-btn:hover {
  border: 2px solid $basic-white;
  background: none;
}

.space-btn {
  border: 2px solid $basic-white;
  color: $basic-white;
  padding: 13px 36px;
  font-style: normal;
  font-weight: 700;
  letter-spacing: 0.6px;
  font-size: 14px;
  border-radius: 23px;
  background: none;
  transition: all .2s ease-in-out;
}

.space-btn:hover {
  background-color: $basic-blue;
  border: 2px solid $basic-blue;
  color: $basic-white;
}

.blue-header-btn {
  background-color: $basic-blue;
  color: $basic-white;
  border: 1px solid $basic-blue;
  padding: 14px 25px;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  border-radius: 5px;
  margin-left: 20px;
  transition: all .2s ease-in-out;
}

.blue-header-btn:hover {
  border: 1px solid $basic-white;
  background: none;
}

.blue-header-btn img {
  padding-left: 15px;
}

