@import 'node_modules/normalize-scss/sass/normalize/_import-now';

@import './assets/fonts';
@import './assets/variables';
@import './assets/mixins';
@import './assets/keyframes';

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  // scroll-behavior: smooth;
}

body {
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  font-family: $basic-font;
  color: $basic-black;
  background-color: $basic-bg-color;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  -moz-osx-font-smoothing: grayscale; 
}

body.lock {
  overflow: hidden;
}

.container {
  max-width: 1050px;
  margin: 0 auto;
  padding: 0 15px;
}

h1,h4,h6, p {
  margin: 0;
}

a {
  text-decoration: none;
  position: relative;
}

@import './components/common/header';
@import './components/common/footer';
@import './components/common/buttons';

@import './components/main/service';
@import './components/main/features';
@import './components/main/industry';
@import './components/main/pricing';
@import './components/main/visit';
@import './components/main/consult';