.visit {
  padding: 90px 0;
  background-image: url('../img/bg/background-contacts.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @include sm-md {
    background-image: url('../img/bg/background-contacts@x0.5.jpg');
    padding: 80px 0;
  }
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    @include sm-md {
      text-align: center;
    }
  }

  &__header {
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    margin-bottom: 45px;
    @include sm-md {
      text-align: center;
    }
  }

  &__inputs input {
    padding: 11px 77px 11px 20px;
    outline:none;
    border: 1px solid $input-bg-color;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: $basic-grey;
  }

  &__inputs input:hover,
  &__inputs select:hover,
  &__message:hover {
    transition: all .2s ease-in-out;
    -webkit-box-shadow: 3px 4px 21px -2px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 3px 4px 21px -2px rgba(34, 60, 80, 0.2);
    box-shadow: 3px 4px 21px -2px rgba(34, 60, 80, 0.2);    
  }



  &__inputs select {
    padding: 11px 151px 11px 15px;
    outline:none;
    border: 1px solid $input-bg-color;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: $basic-grey;
  }

  &__inputs {
    display: grid;
    gap: 20px;
    grid-template-columns: auto auto;
    max-width: 70%;
    margin-bottom: 50px;

    @include sm-md {
      max-width: 100%;
      grid-template-columns: auto;
      gap: 18px;
    }
  }

  &__message {
    grid-column-start: 1;
    grid-column-end: -1;
    height: 140px;
    outline:none;
    border: 1px solid $input-bg-color;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    padding: 16px 0 0 18px;
    resize: none;
  }

  &__btn {
    padding: 15px 40px;
    color: $basic-white;
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    background-color: $basic-blue;
    border-radius: 4px;
    border: 2px;
    transition: all .2s ease-in-out;
    @include sm-md {
      margin: 0 auto;
      display: flex;
      justify-content: center;
      width: 250px;
    }
  }

  &__btn:hover {
    border: 2px solid $basic-blue;
    color: $basic-black;
    background: none;
  }

}