.features {
  background-color: $basic-black;
  color: $basic-white;

  &__container {
    padding: 136px 0;
    display: flex;
    
    justify-content: space-between;

    @include sm-md {
      flex-direction: column;
      padding: 80px 0;
    }
  
  }

  &__block {
    text-align: center;
    padding: 40px 57px;
  }

  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    margin: 27px 0 15px 0;
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
  }
}