.service {
  background-image: url('../img/bg/background.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding-top: 216px;
  padding-bottom: 112px;

  @include sm-md {
    padding: 136px 0 70px 0;
  }

  &__title {
    font-style: normal;
    font-weight: 800;
    font-size: 58px;
    line-height: 80px;
    color: $basic-white;

    @include sm-md {
      font-size: 35px;
      line-height: 38px;
      text-align: center;
    }
  }

  &__description {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    color: $basic-white;
    padding: 40px 0 35px 0;
    @include sm-md {
      padding: 20px 0 35px 0;
      font-size: 17px;
      line-height: 17px;
      text-align: center;
    }
  }

  &__buttons {
    display: flex;
    @include sm-md {
      flex-direction: column;
      justify-content: center;
    }
  }

  &__button {
    margin-right: 15px;
    text-align: center;
    @include sm-md {
      margin-bottom: 10px;
      width: 197px;
      margin: 0 auto 10px auto;
    }
  }

  &__button:last-child {
    margin-right: 0px;
    @include sm-md {
      margin: 0 auto 0 auto;
    }
  }




}